
.demo-item:nth-child(6n) {
  background-color: #8370F4;
}
.demo-item:nth-child(6n+1) {
  background-color: #E46C49;
}
.demo-item:nth-child(6n+2) {
  background-color: #37B0D3;
}
.demo-item:nth-child(6n+3) {
  background-color: #68B33D;
}
.demo-item:nth-child(6n+4) {
  background-color: #E46775;
}
.demo-item:nth-child(6n+5) {
  background-color: #CFC731;
}
.demo-item {
  min-width: 100px;
  margin-left: 3px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 1px 1px 10px #969696;
}
.demo-item .demo-prop-item {
    margin: 10px 5px;
}
.demo-item .demo-prop-item.title {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
.demo-item .demo-prop-item .delete-btn {
      width: 20px;
      height: 20px;
      color: #fff;
      border: none;
      background: 0 0;
      font-size: 17px;
      line-height: 1;
}
.demo-item .demo-prop-item .num-bg {
      display: inline-block;
      width: 20px;
      height: 20px;
      text-align: center;
      color: white;
      font-size: 17px;
}
.demo-item .demo-prop-item .item-prop-text {
      display: block;
      color: white;
}
.demo-item .demo-prop-item input, .demo-item .demo-prop-item select {
      width: 90%;
      font-size: 20px;
}



