
body {
  color: black;
  background-color: #F9F9F9;
  font: 100% Helvetica, sans-serif;
}
body h1, body h2 {
    text-align: center;
    color: #0bf;
}
body #container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    min-height: 500px;
}
body h3 {
    color: #0bf;
}
body .divider {
    border: 1px dashed #999;
}
body button {
    cursor: pointer;
}



